				<p>Krav Maga (u prevodu ''Kontakt Borba'') je sistem samoodbrane nastao 30 tih godina prošlog vijeka na osnovu iskustva Imi Lichtenfelda koji je u to vrijeme bio bokser i hrvač. U vrijeme nacističkog progona jevreja u drugom svijetskom ratu Imi Lichtenfeld osmislio je prve taktičko operativne radnje samoodbrane realne i primjenjive u uličnom nasilju koje kasnije prilikom svoje imigracije u Izrael razvija u vojnim snagama. Ubrzo nakon toga Krav Maga postaje službena borilačka vještina Izraelske vojske a nakon 1964 godine prilagođava se policijskim snagama a kasnije i civilima kao jedan realan i efikasan sistem samoodbrane. Krav Maga se sve do 1996 godine mogla se učiti i trenirati u Izraelu u službama bezbjednosti i civilnim klubovima a od 1996 godine počinje širenje Krav Maga u svijetu počev od Sjedinjenih Američkih Država a ubrzo nakon toga u zemlje Evropske Unije i širom svijeta. Krav Maga takodje biva prihvaćena u mnogim jedinicam policije i vojske u svijetu pa čak i u državama našeg okruženja. U Bosnu i Hercegovinu Krav Maga stiže 2006 godine održavanjem prvog seminara i kursa u Sarajevu od strane Internacionalnog Krav Maga Instituta ''IKMI'' iz Evropske Unije, otkad počinje sa radom sekcija sa redovnim treninzima u sportskom klubu Sfera i na kraju formiranjem ''Krav Maga kluba samoodbrane'' u Sarajevu kao prvog kluba Krav Maga u Bosni i Hercegovini. Krav Maga je sistem samoodbrane koji obuhvata više borilačkih vještina (Karate, Kung-fu, Boks, Jiu-Jitsu). Navedeni sistem samoodbrane se temelji na prirodnim reakcijama čovjeka i njegovim refleksinim pokretima i shodno tome sve tehnike koje Krav Maga obučava su realne i situacionog su karaktera. Kako je sistem nastao u službama bezbjednosti same tehnike su prilagođene uglavnom za upotrebu u vojsci i policiji, medjutim kako se Krav Maga širila na civile razvila je verzije obuke za civile, zaštitare, sistem tehnika za zaštitu trećih i VIP osoba. </p>
